import lib from '@/lib';
let sound = new Audio('static/bell.mp3');

export default {
  alert({ dispatch }, text) {
    return dispatch('alertConfirm', {
      type: 'alert',
      text: text
    });
  },
  confirm({ dispatch }, text) {
    return dispatch('alertConfirm', {
      type: 'confirm',
      text: text
    });
  },
  async alertConfirm({ commit }, { type, text }) {
    return new Promise(resolve => {
      let data = {
        text: text,
        ok: () => {
          resolve(1);
        },
        cancel: () => {
          resolve(0);
        }
      };
      switch (type) {
        case 'alert':
          commit('setAlert', data);
          break;
        case 'confirm':
          commit('setConfirm', data);
          break;
      }
    });
  },
  // fromConnect: æ˜¯å¦å¾ž socket.connect è§¸ç™¼
  async getRoomList({ commit, state, dispatch }, { fromConnect } = {}) {
    // æ­£åœ¨callï¼Œä¸è¦é‡è¤‡call
    if (state.roomListLoading) {
      return;
    }
    state.roomListLoading = true;

    let limit = 20;
    // å¦‚æžœå¾ž socket.connect è§¸ç™¼ï¼Œæ¸…ç©ºåˆ—è¡¨å…¨éƒ¨é‡æ’ˆ
    let roomList = fromConnect ? [] : state.roomList;

    let rooms = await state.api.room.getRoomsList(limit, roomList.length, {
      members: state.client.client
    });

    roomList = roomList.concat(rooms.data);
    commit('setRoomList', roomList);

    // å¦‚æžœå·²ç¶“æ²’æœ‰å¾Œé¢äº†ï¼Œå°±ä¸é¡¯ç¤ºloader
    if (rooms.data && rooms.data.length < limit) {
      commit('setRoomListLoader', false);
    } else {
      commit('setRoomListLoader', true);
    }

    // è¦è‡ªå‹•é€²å…¥æˆ¿é–“
    if (fromConnect) {
      // å¦‚æžœæœ‰ state.roomActive.idï¼Œä»£è¡¨æ˜¯æ–·ç·šé‡é€£
      if (state.roomActive.id) {
        dispatch('setRoomActive', state.roomActive.id);
      } else {
        // å¦‚æžœæœ‰è¨­å®š defaultRoomï¼Œé–‹å•Ÿ defaultRoom
        // å¦‚æžœæ²’æœ‰è¨­å®š defaultRoomï¼Œé–‹å•Ÿç¬¬ä¸€å€‹æˆ¿é–“
        if (state.config.defaultRoom) {
          dispatch('setRoomActive', state.config.defaultRoom);
        } else if (state.roomList.length > 0) {
          dispatch('setRoomActive', state.roomList[0].id);
        }
      }
    }

    state.roomListLoading = false;
  },
  async setRoomActive({ commit, state, dispatch }, roomId) {
    // æ¸…ç©º roomActiveï¼Œå¯èƒ½æ˜¯æ‰‹æ©Ÿè¦è¿”å›žåˆ—è¡¨
    if (!roomId) {
      commit('setRoomActive', {});
      return;
    }

    let room = state.roomList.find(_room => {
      return _room.id === roomId;
    });
    // å¦‚æžœroomåœ¨åˆ—è¡¨è£¡å°±ç›´æŽ¥ setï¼Œä¸åœ¨å°±å« API å
    if (room) {
      commit('setRoomActive', room);
    } else {
      room = await state.api.room.getRoom(roomId);
      if (room) {
        commit('setRoomActive', room);
      } else {
        dispatch('alert', state.i18n.t('Failed to loading group'));
        return;
      }
    }

    state.vue.$log.info('[setRoomActive]', room);

    // å…ˆæ¸…æŽ‰ systemUnreadï¼Œä¸ç„¶åœ¨æ’ˆå¾—éŽç¨‹æœƒè¢«çœ‹åˆ°
    let findRoomChat = state.roomChatList.find(_roomChat => {
      return _roomChat.roomId === roomId;
    });
    if (findRoomChat) {
      findRoomChat.messages = findRoomChat.messages.filter(_message => {
        return _message.messageType !== 'systemUnread';
      });
      commit('setRoomChatListItem', findRoomChat);
    }

    // æ’ˆè¨Šæ¯
    // å¾ž unread çš„å‰5ç­†é–‹å§‹æ’ˆï¼Œè‡³å°‘æ’ˆ20ç­†ï¼Œåªèƒ½ä¸€æ¬¡æ’ˆå›žä¾†ï¼ŒAPIä¸èƒ½åˆ†æ‰¹æ’ˆ...
    // ç¥ˆç¦±ä»¥å¾Œä¸è¦é€²åŒ–æˆåˆ†æ‰¹æ’ˆï¼Œé ­æœƒå¤§å¤§QQ
    let limit = Math.max(room.unread + 5, 20);
    let messages =
      (await state.api.room.getRoomMessages(roomId, limit)).data || [];
    // æ’ˆå›žä¾†çš„è¨Šæ¯é‡å°æ–¼è¦æ±‚çš„æ•¸å­—ï¼Œä»£è¡¨å·²ç¶“åˆ°é ‚å•¦ï¼
    let hasBegin = messages.length < limit;

    // å¦‚æžœæœ‰æœªè®€
    if (room.unread > 0) {
      messages.splice(room.unread, 0, {
        id: `systemUnread-${Date.now()}`,
        messageType: 'systemUnread'
      });
    }

    // å¦‚æžœæœ‰æ’ˆåˆ°è¨Šæ¯ï¼Œé€å·²è®€
    // å–å¾—ç¬¬ä¸€ç­†æœ‰ id çš„è¨Šæ¯ï¼Œè¦é¿å…æ‹¿åˆ°ç³»çµ±è¨Šæ¯
    let lastMessage = messages.find(message => {
      return message.id && !~message.messageType.indexOf('system');
    });
    if (lastMessage) {
      dispatch('updateLastRead', lastMessage);
    }

    // åè½‰ï¼Œå¾žèˆŠåˆ°æ–°
    messages = messages.reverse();

    // æŠŠå‚³é€ä¸­çš„è¨Šæ¯åŠ é€²ä¾†
    let sendingMessages = state.sendingList.filter(message => {
      return message.room === roomId;
    });
    if (sendingMessages) {
      messages = messages.concat(sendingMessages);
    }

    // æŠŠæœ‰ url çš„ textType è½‰æˆurlType
    for (let message of messages) {
      await lib.message.textToUrlType(message);
    }

    let roomChat = {
      roomId: roomId,
      hasBegin: hasBegin, // å·²ç¶“åˆ°é ‚çš„è©±å·è»¸å¾€ä¸Šå°±ä¸ç”¨å†è®€å–
      isLoadingBefore: false, // æ˜¯å¦æ­£åœ¨è®€å–æ›´èˆŠè¨Šæ¯ï¼Œç‚ºäº†é¿å…æ‰‹é€Ÿå¾ˆå¿«çš„å°é­‚ç›ªä¸€ç›´ load
      messages: messages
    };

    commit('setRoomChatListItem', roomChat);
  },
  // å¾€ä¸Šè®€æ›´å¤š
  async getRoomMessagesBefore({ commit, state, dispatch }) {
    let roomId = state.roomActive.id;
    let roomChat = state.roomChatList.find(_roomChat => {
      return _roomChat.roomId === roomId;
    });

    if (!roomChat) {
      dispatch('setRoomActive', roomId);
      return;
    }

    commit('setRoomChatListItemAttribute', {
      roomId: roomId,
      isLoadingBefore: true // æ˜¯å¦æ­£åœ¨è®€å–æ›´èˆŠè¨Šæ¯ï¼Œç‚ºäº†é¿å…æ‰‹é€Ÿå¾ˆå¿«çš„å°é­‚ç›ªä¸€ç›´ load
    });

    let beforeMessage = null;
    if (roomChat.messages && roomChat.messages.length > 0) {
      beforeMessage = roomChat.messages[0].id;
    }

    let limit = 20;
    let messages = (await state.api.room.getRoomMessages(
      roomId,
      limit,
      beforeMessage
    )).data;
    // æ’ˆå›žä¾†çš„è¨Šæ¯é‡å°æ–¼è¦æ±‚çš„æ•¸å­—ï¼Œä»£è¡¨å·²ç¶“åˆ°é ‚å•¦ï¼
    let hasBegin = messages.length < limit;

    // åè½‰ï¼Œå¾žèˆŠåˆ°æ–°
    messages = messages.reverse();

    // æŠŠæœ‰ url çš„ textType è½‰æˆurlType
    for (let message of messages) {
      await lib.message.textToUrlType(message);
    }

    commit('setRoomChatListItemAttribute', {
      roomId: roomId,
      hasBegin: hasBegin, // å·²ç¶“åˆ°é ‚çš„è©±å·è»¸å¾€ä¸Šå°±ä¸ç”¨å†è®€å–
      isLoadingBefore: false // æ˜¯å¦æ­£åœ¨è®€å–æ›´èˆŠè¨Šæ¯ï¼Œç‚ºäº†é¿å…æ‰‹é€Ÿå¾ˆå¿«çš„å°é­‚ç›ªä¸€ç›´ load
    });
    commit('prependRoomChatListItemMessages', {
      roomId: roomId,
      messages: messages
    });
  },
  // å‚³é€è¨Šæ¯ï¼Œæ³¨æ„ï¼šä¸Šå‚³æª”æ¡ˆçš„æµç¨‹ä¸æœƒç¶“éŽé€™é‚Šï¼Œsendingæ©Ÿåˆ¶ä¸ä¸€æ¨£
  async sendMessage({ commit, state, dispatch }, message) {
    let roomId = message.room || state.roomActive.id;
    // å»ºç«‹ sending
    let sendingMessage = Object.assign({}, message);
    sendingMessage.id = `sending-${Date.now()}`;
    sendingMessage.room = roomId;
    sendingMessage.state = 'sending';
    sendingMessage.sender = {
      id: state.client.client
    };

    // åŠ åˆ°æš«å­˜åˆ—è¡¨åŠèŠå¤©è¨Šæ¯åˆ—è¡¨è£¡
    state.sendingList.push(sendingMessage);
    commit('appendRoomChatListItemMessage', sendingMessage);

    // å¦‚æžœæ˜¯ text è¦æª¢æŸ¥æœ‰æ²’æœ‰ urlï¼Œæœ‰çš„è©±è¦è½‰æˆ url type
    // await lib.message.textToUrlType(message);

    dispatch('_sendMessage', {
      roomId,
      message,
      sendingMessage
    });
  },
  // å‚³é€è¨Šæ¯ï¼Œè®Šè¤‡é›œä¸”å¤ªå¤šåœ°æ–¹ç”¨åˆ°äº†ï¼Œç¨ç«‹å‡ºä¾†
  async _sendMessage({ state }, { roomId, message, sendingMessage }) {
    sendingMessage.state = 'sending';
    try {
      // é€å‡º
      await state.api.room.sendMessage(roomId, message);
      // å¾žèŠå¤©è¨Šæ¯åˆ—è¡¨éš±è—ï¼›å¾žæš«å­˜åˆ—è¡¨ç§»é™¤
      sendingMessage.state = 'hide';
      for (let idx in state.sendingList) {
        if (state.sendingList[idx].id === sendingMessage.id) {
          state.sendingList.splice(idx, 1);
          break;
        }
      }
    } catch (e) {
      // å‚³é€å¤±æ•—
      state.vue.$log.error('[sendMessage]', e);
      sendingMessage.roomId = roomId;
      sendingMessage.retryMessage = message;
      sendingMessage.state = 'failed';
    }
  },
  // é‡é€è¨Šæ¯
  async resendMessage({ commit, state, dispatch }, sendingMessage) {
    let ckeck = await dispatch(
      'confirm',
      state.vue.$i18n.t('Resend the message?')
    );
    if (ckeck) {
      dispatch('_sendMessage', {
        roomId: sendingMessage.roomId,
        message: sendingMessage.retryMessage,
        sendingMessage
      });
    }
  },
  // æŽ¥æ”¶è¨Šæ¯
  async receiveMessage({ commit, state, dispatch }, message) {
    let room = state.roomList.find(_room => {
      return _room.id === message.room;
    });

    // å«ä¸€ä¸‹
    if (
      !~state.muteRooms.indexOf(message.room) &&
      message.sender.id !== state.client.client &&
      (!document.hasFocus() || message.room !== state.roomActive.id)
    ) {
      // æ²’æœ‰éœéŸ³ä¸”ä¸æ˜¯è‡ªå·±é€çš„è¨Šæ¯ä¸”ä¸åœ¨æª¢è¦–
      sound.play();
    }

    // æª¢æŸ¥æˆ¿é–“æ˜¯å¦æ˜¯æ­£åœ¨æª¢è¦–çš„
    if (message.room === state.roomActive.id) {
      // æ­£åœ¨æª¢è¦–ï¼šç›´æŽ¥æŠŠè¨Šæ¯åŠ åˆ°è¨Šæ¯é™£åˆ—ä¸­ï¼Œä¸¦ç™¼é€å·²è®€
      commit('appendRoomChatListItemMessage', message);
      dispatch('updateLastRead', message);
    } else if (room && message.sender.id !== state.client.client) {
      // ä¸æ˜¯æ­£åœ¨æª¢è¦–ï¼Œä½† room å·²åœ¨åˆ—è¡¨ï¼Œä¸”ä¸æ˜¯è‡ªå·±ç™¼çš„è¨Šæ¯ï¼šæ›´æ–° unreaded æ•¸å­—
      room.unread = (room.unread || 0) + 1;
    }

    // æŠŠæˆ¿é–“æ‹‰åˆ°åˆ—è¡¨æœ€ä¸Šé¢
    if (room) {
      // room å·²åœ¨åˆ—è¡¨ä¸­ï¼Œæ›´æ–° lastMassage ä¸¦æŽ’åºåˆ—è¡¨
      room.lastMessage = message;
      commit(
        'setRoomList',
        state.roomList.sort((a, b) => {
          if (!a.lastMessage) {
            return 1;
          }
          if (!b.lastMessage) {
            return -1;
          }
          if (a.lastMessage.id > b.lastMessage.id) {
            return -1;
          }
          if (a.lastMessage.id < b.lastMessage.id) {
            return 1;
          }
        })
      );
    } else {
      // æˆ¿é–“ä¸åœ¨åˆ—è¡¨è£¡ï¼šæ’ˆæˆ¿é–“è³‡è¨Šæ’åˆ°æœ€ä¸Šé¢
      room = await state.api.room.getRoom(message.room);
      if (room) {
        let findRoomAgain = state.roomList.find(_room => {
          return _room.id === room.id;
        });
        if (!findRoomAgain) {
          state.roomList.unshift(room);
        }
      }
    }

    // æ˜¯åŠ å…¥æˆ¿é–“çš„è¨Šæ¯
    if (~['joinRoom', 'addMember'].indexOf(message.messageType)) {
      // ä¸æ˜¯è‡ªå·±è¢«åŠ å…¥
      if (message.member.id !== state.client.client) {
        let memberIdx = room.members.findIndex(_member => {
          return _member.id === message.member.id;
        });
        if (!~memberIdx) {
          room.members.push(message.member);
        }
      }
    }

    // æ˜¯é‚€è«‹è¨Šæ¯
    if (~['addMembers'].indexOf(message.messageType)) {
      let members = JSON.parse(message.message);
      members.forEach(member => {
        // ä¸æ˜¯è‡ªå·±è¢«åŠ å…¥
        if (member.id !== state.client.client) {
          let memberIdx = room.members.findIndex(_member => {
            return _member.id === member.id;
          });
          if (!~memberIdx) {
            room.members.push(member);
          }
        }
      });
    }

    // æ˜¯é›¢é–‹æˆ¿é–“çš„è¨Šæ¯
    if (~['leaveRoom', 'deleteMember'].indexOf(message.messageType)) {
      if (message.member.id === state.client.client) {
        // è‡ªå·±è¢«ç§»é™¤äº†ï¼ŒæŠŠæˆ¿é–“å¾žåˆ—è¡¨ç§»é™¤
        let roomIdx = state.roomList.findIndex(_room => {
          return _room.id === room.id;
        });
        if (~roomIdx) {
          state.roomList.splice(roomIdx, 1);
        }
        if (state.roomActive.id === room.id) {
          // æ­£åœ¨æª¢è¦–æˆ¿é–“ï¼šè¦é—œé–‰æª¢è¦–
          setTimeout(() => {
            dispatch('setRoomActive', null);
          }, 1);
        }
      } else {
        // åˆ¥äººè¢«ç§»é™¤äº†ï¼ŒæŠŠäººå¾žåˆ—è¡¨ç§»é™¤
        let memberIdx = room.members.findIndex(_member => {
          return _member.id === message.member.id;
        });
        if (~memberIdx) {
          room.members.splice(memberIdx, 1);
        }
      }
    }
  },
  // ç™¼é€å·²è®€
  async updateLastRead({ state }, message) {
    let roomId = state.roomActive.id;
    await state.api.room.updateLastRead(roomId, message.id);
    let room = state.roomList.find(_room => {
      return _room.id === roomId;
    });
    if (room) {
      room.unread = 0;
    }
  },
  // æˆ¿é–“è³‡è¨Šæ”¹è®Šï¼ŒåŒ…å«æ”¶åˆ°å·²è®€
  async receiveRoom({ state }, room) {
    let findRoom = state.roomList.find(_room => {
      return _room.id === room.id;
    });
    if (!findRoom) {
      return;
    }

    if ('name' in room) {
      findRoom.name = room.name;
    }
    if ('description' in room) {
      findRoom.description = room.description;
    }
    if ('cover' in room) {
      findRoom.cover = room.cover;
    }
    // if ('memberProperty' in room) {
    //   let findMemberProperty = findRoom.memberProperties.find(
    //     _memberProperty => {
    //       return _memberProperty.client === room.memberProperty.client;
    //     }
    //   );
    //   if (findMemberProperty) {
    //     findMemberProperty.badge = room.memberProperty.badge;
    //     findMemberProperty.lastRead = room.memberProperty.lastRead;
    //   } else {
    //     findRoom.memberProperties.push(room.memberProperty);
    //   }
    //   // è‡ªå·±é€çš„å·²è®€ï¼Œæœªè®€æ¨¹è¦æ­¸ 0
    //   if (room.memberProperty.client === state.client.client) {
    //     findRoom.unread = 0;
    //   }
    // }
  },
  // æ”¶åˆ°å·²è®€
  async receivelastRead({ state }, data) {
    let findRoom = state.roomList.find(_room => {
      return _room.id === data.roomID;
    });
    if (!findRoom) {
      return;
    }

    let findMemberProperty = findRoom.memberProperties.find(_memberProperty => {
      return _memberProperty.client === data.memberID;
    });
    if (findMemberProperty) {
      findMemberProperty.lastRead = data.messageID;
    }
    // è‡ªå·±é€çš„å·²è®€ï¼Œæœªè®€æ¨¹è¦æ­¸ 0
    if (data.memberID === state.client.client) {
      findRoom.unread = 0;
    }
  },
  async deleteMember({ state }, clientId) {
    await state.api.room.deleteMember(state.roomActive.id, clientId);
  },
  async addMembers({ state }, invitees) {
    await state.api.room.addMembers(state.roomActive.id, invitees);
  },
  async createAndJoinRoom({ state, dispatch }, roomInfo) {
    let res = await state.api.room.createAndJoinRoom(roomInfo);
    res.unread = 0;
    dispatch('setRoomActive', res.id);
    if (res.lastMessage) {
      // æ–°æˆ¿é–“æœ‰è¨Šæ¯ï¼ŒæŠŠæ–°æˆ¿é–“æ’åˆ°åˆ—è¡¨ç¬¬ä¸€å€‹
      state.roomList.unshift(res);
    } else if (!state.roomListLoader) {
      // ç¾æœ‰æˆ¿é–“åˆ—è¡¨å·²ç¶“å…¨æ’ˆå‡ºä¾†äº†ï¼ŒæŠŠæ–°æˆ¿é–“å¡žåˆ°åˆ—è¡¨æœ€ä¸‹é¢
      state.roomList.push(res);
    }
  },
  async updateRoomInfo({ state }, { roomId, roomInfo }) {
    let res = await state.api.room.updateRoom(roomId, roomInfo);
    state.vue.$log.test('[updateRoomInfo]', res);
  },
  async uploadFiles({ state, dispatch }, event) {
    let files;
    files =
      event.target.files || (event.dataTransfer && event.dataTransfer.files);

    let actions = state.config.chat.actions;
    for (let file of files) {
      let type = file.type;
      let limitSize = 0;
      // æª¢æŸ¥æ”¯æ´åº¦åŠå–å¾—æª”æ¡ˆå¤§å°é™åˆ¶
      if (actions.image.enable && ~actions.image.extra.accept.indexOf(type)) {
        limitSize = actions.image.extra.limitSize;
      } else if (
        actions.video.enable &&
        ~actions.video.extra.accept.indexOf(type)
      ) {
        limitSize = actions.video.extra.limitSize;
      } else if (
        actions.file.enable &&
        ~actions.file.extra.accept.indexOf(type)
      ) {
        limitSize = actions.file.extra.limitSize;
      } else {
        // ä¸æ”¯æ´
        await dispatch(
          'alert',
          state.i18n.t('Unsupported file type: {filename}', {
            filename: file.name
          })
        );
        continue;
      }
      // è¶…éŽå¤§å°
      if (file.size > limitSize * 1048576) {
        await dispatch(
          'alert',
          state.i18n.t("'{filename}' is over {size}MB", {
            filename: file.name,
            size: limitSize
          })
        );
        continue;
      }
      dispatch('uploadFile', file);
    }

    if (
      event.target instanceof HTMLInputElement &&
      event.target.type === 'file'
    ) {
      event.target.value = '';
    }
  },
  async uploadFile({ commit, state, dispatch }, file) {
    let type = file.type.split('/')[0];
    let extra = JSON.stringify({
      mimeType: file.type,
      fileExtension: file.name
        ? file.name.slice(((file.name.lastIndexOf('.') - 1) >>> 0) + 2)
        : '', // Magic!
      bytes: file.size
    });
    let uploadingMessage = {
      room: state.roomActive.id,
      id: `uploading-${Date.now()}-${file.name}`,
      state: 'uploading',
      progress: 0,
      sender: {
        id: state.client.client
      },
      message: file.name
        ? file.name.slice(0, (file.name.lastIndexOf('.') - 1) >>> 0)
        : '',
      extra: extra
    };
    let message = {
      room: state.roomActive.id,
      message: file.name
        ? file.name.slice(0, (file.name.lastIndexOf('.') - 1) >>> 0)
        : '',
      extra: extra
    };

    message.extra = extra;
    let fileThumbnail = {
      originalFile: file,
      thumbnailFile: null,
      fileWidth: 0,
      fileHeight: 0
    };

    switch (type) {
      case 'image':
      case 'video':
        fileThumbnail =
          type === 'image'
            ? await lib.upload.getThumbnailFromImage(file)
            : await lib.upload.getThumbnailFromVideo(file);

        uploadingMessage.messageType = type;
        uploadingMessage.width = fileThumbnail.fileWidth;
        uploadingMessage.height = fileThumbnail.fileHeight;
        uploadingMessage.thumbnailUrl = URL.createObjectURL(
          fileThumbnail.thumbnailFile
        );

        if (fileThumbnail.duration) {
          uploadingMessage.duration = fileThumbnail.duration;
          message.duration = fileThumbnail.duration;
        }
        message.messageType = type;
        message.width = fileThumbnail.fileWidth;
        message.height = fileThumbnail.fileHeight;
        break;
      case 'audio':
        let audioInfo = await lib.upload.getInfoFromAudio(file);

        uploadingMessage.messageType = type;
        if (audioInfo.duration) {
          uploadingMessage.duration = audioInfo.duration;
          message.duration = audioInfo.duration;
        }
        message.messageType = type;
        break;
      default:
        uploadingMessage.messageType = 'file';
        message.messageType = 'file';
        break;
    }

    // uploading åŠ åˆ°æš«å­˜åˆ—è¡¨åŠèŠå¤©è¨Šæ¯åˆ—è¡¨è£¡
    state.sendingList.push(uploadingMessage);
    commit('appendRoomChatListItemMessage', uploadingMessage);

    let totalFileSize = fileThumbnail.originalFile.size;
    if (fileThumbnail.thumbnailFile) {
      totalFileSize += fileThumbnail.thumbnailFile.size;
    }

    // ä¸Šå‚³åŽŸæª”
    let originalRes = await state.api.file.upload(
      state.config.bucketName,
      fileThumbnail.originalFile,
      {},
      e => {
        uploadingMessage.progress = Math.round(
          (e.loaded * 100) / totalFileSize
        );
      }
    );
    message.originalUrl = originalRes;
    // ä¸Šå‚³ç¸®åœ–
    if (fileThumbnail.thumbnailFile) {
      let thumbnailRes = await state.api.file.upload(
        state.config.bucketName,
        fileThumbnail.thumbnailFile,
        {},
        e => {
          uploadingMessage.progress = Math.round(
            ((fileThumbnail.originalFile.size + e.loaded) * 100) / totalFileSize
          );
        }
      );
      message.thumbnailUrl = thumbnailRes;
    }

    // é€å‡ºè¨Šæ¯
    dispatch('_sendMessage', {
      roomId: message.room,
      message,
      sendingMessage: uploadingMessage
    });
  },
  async sendTyping({ state }, message) {
    state.socket.typing(state.roomActive.id, message);
  },
  receiveTyping({ state }, typing) {
    if (typing.sender === state.client.client) {
      return;
    }
    state.typingList.push(typing);
    setTimeout(() => {
      let index = state.typingList.indexOf(typing);
      if (index > -1) {
        state.typingList.splice(index, 1);
      }
    }, 6000);
  },
  async getMuteRooms({ commit, state }) {
    let info = await state.api.me.getInfo();
    commit('setMuteRooms', info.mute);
  },
  async muteRoom({ state, dispatch }) {
    await state.api.me.muteRoom(state.roomActive.id);
    dispatch('getMuteRooms');
  },
  async unMuteRoom({ state, dispatch }) {
    await state.api.me.unMuteRoom(state.roomActive.id);
    dispatch('getMuteRooms');
  },
  async subscribe({ commit, state }, token) {
    await state.api.me.subscribe('web', token);
    localStorage.setItem('enableNotification', '1');
    commit('setEnableNotification', true);
  },
  async unsubscribe({ commit, state }, token) {
    await state.api.me.unsubscribe('web', token);
    localStorage.setItem('enableNotification', '0');
    commit('setEnableNotification', false);
  }
};



